.sectionExpenses {
    margin-bottom: 65px;

    .ulExpenses {
        position: relative;
        width: 450px;
        max-width: 750px;
        left: 50%;
        transform: translateX(-50%);

        .nonExpenses {
            text-transform: uppercase;
            font-weight: 700;
            text-align: center;
            font-size: clamp(10px, 10px + 3vw, 50px);
        }
    }

    .expensesCalculating {
        position: fixed;
        background-color: rgb(228, 226, 226);
        height: 45px;
        bottom: 0;
        width: 100%;
        margin-top: 20px;
        border-top: 1px solid black;
        border-radius: 10px 10px 0 0;
        text-align: center;
        font-weight: normal;
        font-size: large;
        box-shadow: 3px 2px 6px black;
    }

    .sort {
        margin-left: 10px;

        .sortSelect {
            padding: 2px;
            margin-left: 5px;
            border-radius: 10px;
        }
    }
}